(function (argument) {
	var MergeAcquistion = {
		CurrentArray: [],
		MonthNames: ["January", "February", "March", "April", "May", "June", "July", "August", "September", "October", "November", "December"],
		RenderDesktopVersion: function(data, Parent) {
			Parent.append('<tbody class="visible-lg"></tbody>');

			var Wrapper = Parent.find('tbody.visible-lg');

			for(var key in data) {
				Wrapper.append('<tr></tr>');				
				Wrapper.find('tr:last-child').append('<td align="left" type="date" month="'  +data[key].deal_month+ '" class="R16 pad-10">' +this.MonthNames[data[key].deal_month - 1]+ '</td>');
				Wrapper.find('tr:last-child').append('<td align="left" type="text" class="R16 pad-10">'+data[key].acquirer_company_name+'</td>');
				Wrapper.find('tr:last-child').append('<td align="left" type="text" class="R16 pad-10">'+data[key].target_company_name+'</td>');
				Wrapper.find('tr:last-child').append('<td align="left" type="text" class="R16 pad-10">'+data[key].target_sector+'</td>');
				Wrapper.find('tr:last-child').append('<td align="left" type="text" class="R16 pad-10">'+data[key].target_location+'</td>');
				Wrapper.find('tr:last-child').append('<td align="right" type="text" class="R16 pad-10">'+data[key].details+'</td>');
				if(data[key].price) {
					var Price = data[key].price;
				} else {
					Price = '--';
				}
				Wrapper.find('tr:last-child').append('<td align="right" type="number" class="R16 pad-10">'+Price+'</td>');
			}

		},
		init: function(data, Parent) {
			this.RenderDesktopVersion(data, Parent);
			//this.RenderMobileVersion(data, Parent);
		}
	}

	if($('.merge-acquistion').length > 0) {
		MergeAcquistion.init(window.jsonMergeAcquistion, $('.merge-acquistion'));
	}
})();